
import { React, useEffect, useState } from "react";
import { Col, Row } from "reactstrap";
import json from "../services/jsonData.json";
import { useNavigate } from "react-router-dom";
import TitleCard from "./TitleCard";

const Starter = () => {
  let navigate = useNavigate();
  const [jsonData, setJsonData] = useState({ chapters: [] })
  const [favsCount, setfavsCount] = useState(0)

  useEffect(() => {
    json.chapters.map((item, i) => {
      if (localStorage.getItem('id_' + i) === '1')
        item.favorite = 1;
    })
    setJsonData(json)
    console.log(json)
  }, [])

  useEffect(() => {
    let favorited = localStorage.getItem('fav');
    if (favorited) {
      let parsefavorited = JSON.parse(favorited)
      setfavsCount(parsefavorited.length)
    }
  }, [])

  return (
    <div className="starter">
      <Row>
      <TitleCard />

      </Row>
      <Row>
        <Col style={{ cursor: 'pointer', padding: '5px' }} xs="12" sm="12" md="12" lg="12" xl="12" xxl="12">
          <div class="card card-body" style={{ height: '100%', marginBottom: '0px', fontSize: '13px', textAlign: 'justify' }}>
            <h5>About the app</h5>
            <p>
              ‘Ashtanga Hridayam Kalpasiddhisthanam - Learning sloka made simple’ audio software application has been designed for BAMS, PG, Ph.D. students, teaching faculties and general practitioners as a part of Ph.D. research work by Dr. Braivin Camly R MD (Ay), Professor, Department of Samhita, Siddhanta and Sanskrit, Govt. Ayurveda College, Thiruvananthapuram under the guidance of Dr. Akshar Ashok Kulkarni MD (Ay), Ph.D., Professor, Department of Samhita, Siddhanta and Sanskrit, Parul Institute of Ayurveda, Parul University, Vadodara, Gujarat.
            </p>
            <p>
              This app contains Ashtanga Hridaya audio-based sutra with an explanation that includes anvaya, bhavartha, sandhi, samasa, and tantrayukti. According to a recent course curriculum revision by NCISM, the board incorporated Samhita pathana as the first step and the most effective method of Samhita teaching. This audio software app will be beneficial for teaching and learning Samhita.
            </p>
            <p>
              The objective of this app is to enable students to effortlessly master the slokas by comprehending them and practicing the same regularly. The soothing audio clips and detailed sutra explanations on this app equip the students to understand, by heart, and recall the sutras easily.
            </p>
            <p style={{ margin: '0', textAlign: 'center' }}>
              {/* <a onClick={() => navigate("/privacy-policy")}>privacy policy</a> */}
            </p>
          </div>
        </Col>
      </Row>
      <Row>
        <Col style={{ cursor: 'pointer', padding: '5px' }} xs="12" sm="12" md="12" lg="12" xl="12" xxl="12">
          <div onClick={() => navigate("/gallery/")} class="card card-body" style={{ height: '100%', marginBottom: '0px', textAlign: 'center', fontSize: '13px' }}>
            <h3><i class="bi bi-book-half"></i> Chapters </h3>
            <p style={{ margin: '0' }}> {jsonData?.chapters?.length} Chapters</p>
          </div>
        </Col>
        <Col style={{ cursor: 'pointer', padding: '5px' }} xs="12" sm="12" md="12" lg="12" xl="12" xxl="12">
          <div onClick={() => navigate("/fav/")} class="card card-body" style={{ height: '100%', marginBottom: '0px', textAlign: 'center', fontSize: '13px' }}>
            <h3><i class="bi bi-heart-fill"></i> Favorite</h3>
            <p style={{ margin: '0' }}> {favsCount} Chapters</p>
          </div>
        </Col>
        <Col style={{ cursor: 'pointer', padding: '5px' }} xs="12" sm="12" md="12" lg="12" xl="12" xxl="12">
          <div onClick={() => navigate("/about/")} class="card card-body" style={{ height: '100%', marginBottom: '0px', textAlign: 'center', fontSize: '14px' }}>
            <h3 ><i class="bi bi-person-fill"></i> About the author</h3>
          </div>

        </Col>

      </Row>
      <div style={{textAlign:'center'}}>
        <a
          href="https://www.termsfeed.com/live/4bcd6102-fe13-4e43-914c-10d9cc733351"
          target="_blank"
          rel="noopener noreferrer"
          style={{ color: "white", textDecoration: "underline", fontSize:"18px" , padding:'10px'}}
        >
          Privacy Policy
        </a>
      </div>
    </div>
  );
};

export default Starter;
